<template>
  <div class="container">
    <router-link to="/mychannels" class="back-btn">←</router-link>

    <div class="channel-header">
      <h1 class="channel-title">{{ group.username }}</h1>
      <div class="channel-info-grid">
        <div class="info-card">
          <div class="info-number">{{ subscribers }}</div>
          <div class="info-label">Подписчики</div>
        </div>
        <div class="info-card">
          <div class="info-number">{{ views }}</div>
          <div class="info-label">Просмотры</div>
        </div>
      </div>
      <div class="channel-actions">
        <button class="action-btn secondary" @click="editGroup">Изменить</button>
        <button class="action-btn primary" @click="toggleEnable">
          {{ group.is_owner ? 'Отключить канал' : 'Включить канал' }}
        </button>
      </div>
    </div>

    <div class="schedule-section">
      <div class="schedule-title">Расписание публикаций</div>
      <div class="schedule-grid" ref="grid">
        <!-- Динамически заполняется в initSchedule -->
      </div>
      <button class="action-btn primary" @click="saveSchedule" style="margin-top: 16px;">
        Сохранить расписание
      </button>
    </div>
  </div>
</template>

<script setup>
import { ref, reactive, onMounted } from 'vue'
import { useAppStore } from '@/stores/app'
import { useRoute } from 'vue-router'

const appStore = useAppStore()
const route = useRoute()
const groupId = Number(route.params.id)

const group = reactive({
  username: '',
  subject_ids: [],
  country: '',
  language: '',
  link: '',
  is_owner: false,
  ad_prices: {},
  all_slots: {},
  busy_slots: {}
})

const subscribers = ref(0)
const views = ref(0)

// HTTP headers helper
const headers = () => ({
  'Content-Type': 'application/json',
  Authorization: 'Telegram-Init-Data ' + appStore.tgInitDataRaw
})

// Инициализировать расписание
function initSchedule() {
  const grid = gridRef.value
  grid.innerHTML = ''
  const days = Object.keys(group.all_slots)
  const hours = Array.from({ length: 24 }, (_, i) => i)

  // Первая пустая ячейка
  const empty = document.createElement('div')
  empty.className = 'schedule-time-header'
  grid.appendChild(empty)

  // Заголовки дней (keys)
  days.forEach(day => {
    const el = document.createElement('div')
    el.className = 'schedule-day-header'
    el.textContent = day
    grid.appendChild(el)
  })

  hours.forEach(hour => {
    const label = document.createElement('div')
    label.className = 'schedule-time-label'
    label.textContent = hour
    grid.appendChild(label)

    days.forEach(day => {
      const cell = document.createElement('div')
      cell.className = 'schedule-cell'
      cell.dataset.day = day
      cell.dataset.hour = hour
      // отмечаем занятые слоты
      if (group.busy_slots[day]?.includes(hour)) {
        cell.classList.add('active')
      }
      cell.addEventListener('click', () => cell.classList.toggle('active'))
      grid.appendChild(cell)
    })
  })
}

// Сохранить расписание
async function saveSchedule() {
  const grid = gridRef.value.children
  const schedule = {}
  // собираем по дням
  for (const cell of grid) {
    if (!cell.dataset.day) continue
    const { day, hour } = cell.dataset
    if (cell.classList.contains('active')) {
      schedule[day] = schedule[day] || []
      schedule[day].push(Number(hour))
    }
  }
  try {
    await fetch(
      `${appStore.apiBaseUrl}/api/group/${groupId}/schedule`,
      {
        method: 'PUT',
        headers: headers(),
        body: JSON.stringify({ schedule }),
        mode: 'cors'
      }
    )
    alert('Расписание сохранено')
  } catch (e) {
    console.error('Ошибка сохранения расписания:', e)
  }
}

// Редактирование группы (заглушка)
function editGroup() {
  // можно перенаправить на форму редактирования
  console.log('Edit group', groupId)
}

function toggleEnable() {
  // Тоггл статуса; простая заглушка
  group.is_owner = !group.is_owner
}

const gridRef = ref(null)

onMounted(async () => {
  await appStore.initTelegram()
  // получаем info по группе
  try {
    const res = await fetch(
      `${appStore.apiBaseUrl}/api/group/${groupId}`,
      { headers: headers(), mode: 'cors' }
    )
    const data = await res.json()
    Object.assign(group, data)
    // если в ответе есть статистика
    subscribers.value = group.all_slots?.additionalProp1?.length || 0
    views.value = group.all_slots?.additionalProp2?.length || 0
    // инициализируем расписание
    initSchedule()
  } catch (e) {
    console.error('Ошибка загрузки группы:', e)
  }
})
</script>